import React from 'react';
import Input from './input';

const ContactForm = () => {
    return (
        <form>
            <Input
                name={'nom'}
                label={'Nom'}
                type={'text'}
            />
            <Input
                name={'prenom'}
                label={'Prenom'}
                type={'text'}
            />
            <Input
                name={'email'}
                label={'email'}
                type={'email'}
            />
            <Input
                name={'telephone'}
                label={'Telephone'}
                type={'number'}
            />
            <div className="contactform__message__textarea">
                <label>Message</label>
                <textarea />
            </div>
            <button>Envoyer</button>
        </form>
    );
};

export default ContactForm;